.header {
  padding-top: 30px;
  padding-bottom: 32px;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logotype {
  z-index: 2;
}

.nav__list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.nav__link {
  font-family: var(--font-family);
  color: #fff;
  opacity: .8;
  font-size: 14px;
  font-weight: 400;
  line-height: 125%;
  transition: opacity, all .25s, all cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.nav__link:hover {
  opacity: .5;
  transition-duration: .25s;
}

.header__mobilmenu {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #191919;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 100px;
  padding-left: 20px;
  transition: transform .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.header__mobilmenu.open {
  transform: translateX(0);
}

.header__btn {
  z-index: 2;
  width: 22px;
  height: 12px;
  position: relative;
}

.header__btn:before, .header__btn:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  position: absolute;
  left: 0;
}

.header__btn:before {
  top: 0;
}

.header__btn:after {
  bottom: 0;
}

.header__btn--icon {
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: transform .5s;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.open ~ .header__btn .header__btn--icon {
  transform: translateY(-50%)rotate(45deg);
}

.open ~ .header__btn:before {
  top: 50%;
  transform: translateY(-50%)rotate(45deg);
}

.open ~ .header__btn:after {
  top: 50%;
  transform: translateY(-50%)rotate(-45deg);
}

@media (min-width: 768px) {
  .header {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .nav__link {
    font-size: 16px;
  }

  .header__mobilmenu {
    width: 100%;
    height: 370px;
    padding-top: 120px;
    padding-left: 60px;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0 20px 80px #000;
  }
}

@media (min-width: 1200px) {
  .header {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  .header__mobilmenu {
    height: auto;
    width: auto;
    box-shadow: none;
    padding-top: 0;
    padding-left: 0;
    display: flex;
    position: static;
    transform: translate(0);
  }

  .header__btn {
    display: none;
  }

  .nav__list {
    background-color: #191919;
    flex-direction: row;
    gap: 77px;
  }

  .nav__link:hover {
    opacity: .8;
  }

  .nav__link:after {
    content: "";
    width: 100%;
    height: 4px;
    transform-origin: 100%;
    background-color: #fff;
    transition-property: transform;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    bottom: -15px;
    left: 0;
    transform: scaleX(0);
  }

  .nav__link:hover:after {
    transform-origin: 0;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    transform: scaleX(1);
  }
}

